
.code pre {
    margin: 0;
}

.code {
    background-color: rgb(43,43,43);
    padding-left: 0px;
    padding-top: 2px;
    padding-bottom: 2px;
    width: 70%;
    overflow-x: scroll;
    border: 1px solid black;
    font-size: 20px;
    scrollbar-color: #1c1f24 #1c1f24;
    scrollbar-track-color: #1c1f24 #1c1f24;
}

.code-title {
    width: 100%;
    text-align: center;
    background-color: rgb(49,51,53);
    padding-left: 10px;
    padding-bottom: 3px;
    padding-right: 40px;
    border: 1px solid black;
    border-top: 0px solid black;
    user-select: none;
}

.line-identifier {
    user-select: none;
    cursor: pointer;
    background-color: rgb(49,51,53);
    margin-right: 12px;
    padding-left: 10px;
    padding-right: 12px;
    border-right: 1px solid black;
    color: rgb(96, 99, 102);
}

.code span::selection, .code span::-moz-selection {
    background: rgb(33, 66, 131);
}

.java span::selection, .java br::selection {
    background: rgb(33, 66, 131);
}

.java br {
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}