
a {
    color: aqua;
}


.menu-item {
    display: block;
    width: calc(100% - 20px);
    padding-top: 20px;
    padding-bottom: 20px;
    text-align: left;
    padding-left: 20px;
    background-color: #1c1f24;

    display: flex;

    cursor: pointer;
}

#active {
    background-color: #262b32;
}

.menu-item > a {
    color: #fff;
    text-decoration: none;
    font-size: min(3.5vw, 40px);
}

.menu-item:hover {
    background-color: #262b32;
}

.menu-parent {
    top: 0;
    left: 0;
    position: fixed;
    list-style-type: none;
    margin: 0;
    padding: 0;
    width: 20%;
    height: 100%;
    background-color: #1c1f24;
    max-width: 300px;
}

.menu-bottom {
    bottom: 0px;
    width: 20%;
    max-width: 300px;
    height: 40px;
    background-color: #262b32;
    position:fixed;
    /* padding-left: 30px; */
    padding-top: 5px;
    font-size: min(2.2vw, 25px);
    color: gray;

    text-align: center;
    
    border-top-right-radius: 5px;
    border-top-left-radius: 5px;
}